<template>
  <div id="post-blog">
    <v-form>
      <v-container>
        <v-row>
          <v-col cols="12" sm="6">
            <v-text-field v-model="title" label="" filled></v-text-field>
            <p v-if="checkTitle" class="error-msg"></p>
          </v-col>

          <v-col cols="12" sm="6">
            <v-text-field v-model="subtitle" label="" filled></v-text-field>
            <p v-if="checkSubTitle" class="error-msg"></p>
          </v-col>
        </v-row>

        <v-col col="12">
          <v-textarea color="teal" rows="10" v-model="input" @input="compileMarkdown()">
            <template v-slot:label></template>
          </v-textarea>
          <p v-if="checkContent" class="error-msg"></p>
        </v-col>

        <v-col cols="12" sm="6">
            <v-text-field v-model="password" label="" filled></v-text-field>
            <p v-if="checkSubTitle" class="error-msg"></p>
          </v-col>

        <v-card-actions class="justify-center">
          <v-btn class="white--text" color="deep-purple accent-4" @click="submit()">
            
          </v-btn>
        </v-card-actions>

        <div v-html="renderHTML"></div>
      </v-container>
    </v-form>
  </div>
</template>

<script>
import markdownIt from "markdown-it";
import hljs from "highlight.js";
import sanitizer from "markdown-it-sanitizer";
import emoji from "markdown-it-emoji";
import katex from "@iktakahiro/markdown-it-katex";
import checkbox from "markdown-it-checkbox";
import sub from "markdown-it-sub";
import container from "markdown-it-container";
import ins from "markdown-it-ins";
import mark from "markdown-it-mark";
import footnote from "markdown-it-footnote";
import deflist from "markdown-it-deflist";
import abbr from "markdown-it-abbr";
import axios from 'axios';

export default {
  name: "PostBlog",
  data() {
    const md = new markdownIt({
      highlight: function (code, lang) {
        return hljs.highlightAuto(code, [lang]).value;
      },
      html: true,
      linkify: true,
      breaks: true,
      typographer: true,
    })
      .use(katex, { throwOnError: false, errorColor: " #cc0000" })
      .use(emoji)
      .use(checkbox)
      .use(sanitizer)
      .use(sub)
      .use(ins)
      .use(mark)
      .use(footnote)
      .use(deflist)
      .use(abbr)
      .use(container, "info")
      .use(container, "success")
      .use(container, "warning")
      .use(container, "danger");

    return {
      markdownIt: md,
      sourceText: String,
      renderHTML: "",
      input: "",
      title: "",
      subtitle: "",
      password: "",
      submitted: false 
    };
  },
  methods: {
    compileMarkdown: function () {
      this.renderHTML = this.markdownIt.render(this.input);
    },
    submit: function () {
      if (!this.title || !this.subtitle || !this.input || !this.password) {
        alert('');
        return;
      }

      axios
      .post('api/blog/submit', {
        title: this.title,
        subtitle: this.subtitle,
        content: (this.renderHTML.replace(/\r?\n/g, "<br>")),
        password: this.password
      })
      .then(response => {
        alert('');
      })
      .catch(error => {
        if (error.response.status === 403) {
          alert('');
        }
        else if (error.response.status === 500) {
          alert('');
        }
        else {
          alert(`StatusCode: ${error.response.status}`);
        }
      });
    }
  },
  computed:{
    checkTitle(){
      return this.title.length < 1;
    },
    checkSubTitle(){
      return this.subtitle.length < 1;
    },
    checkContent(){
      return this.input.length < 1;
    }
  }
};
</script>

